import Vue from 'vue';
import api from '../api';

const h2bStates = {
    READY: 'ready',
    SENDING: 'sending',
};

const h2bStore = {
    // Initial state
    state: {
        received: {},
        sent: {},
        token: '',
        status: h2bStates.READY
    },

    // Actions
    actions: {
        h2bStartSession ({dispatch, commit} ) {
            commit('h2bSetStatus', h2bStates.SENDING);
            return api.post( api.routes.message, {} ).do((response) => {
                commit('h2bReceive', response);
                commit('h2bStartSession', response.token);
                commit('h2bSetStatus', h2bStates.READY);

                // Custom for OBA Wijzer
                dispatch('appParseH2B', response);
                // --
            }).toPromise();
        },
        h2bCloseSession ({dispatch, commit}) {
            commit('h2bCloseSession');

            // Custom for OBA Wijzer
            dispatch('appReset');
            // --
        },
        h2bSend ({dispatch, commit}, message) {
            commit('h2bSetStatus', h2bStates.SENDING);
            commit('h2bHasSent', message);

            return api.post( api.routes.message, message ).do( (response) => {
                commit('h2bReceive', response);
                commit('h2bSetStatus', h2bStates.READY);

                // Custom for OBA Wijzer
                if (message.message === "_close_") {
                    dispatch('h2bCloseSession').then(() => {
                        dispatch('h2bStartSession');
                    });
                }
                // --
            }).toPromise();
        },
    },

    // Mutations
    mutations: {
        h2bSetStatus (state, status) {
            state.status = status;
        },
        h2bHasSent (state, message) {
            state.sent = message;
        },
        h2bReceive (state, message) {
            state.received = message;
        },
        h2bStartSession (state, token) {
            state.token = token;
            state.status = h2bStates.READY;
        },
        h2bCloseSession (state) {
            state.received = {};
            state.sent = {};
            state.token = '';
            state.status = h2bStates.READY;
        }
    },

    // Getters
    getters: {
        h2bCanSend: state => {
            return (state.status == h2bStates.READY);
        }
    }
};

export default h2bStore;


