import Vue from 'vue';
import api from '../../services/api';
import globals from '../../globals';

const appStore = {
    // Initial state
    state: {
        template: 'loading',
        question: '',
        answers: [],
        meta: {},
        membershipId: '',
        subscriptionList: {},
        selectedSubscription: globals.subscriptions.basis,
        advisedSubscription: '',
    },

    // Actions
    actions: {
        appLoadSubscriptionList ({commit}) {
            return api.get( api.routes.subscriptionList ).do((response) => {
                commit('appLoadSubscriptionList', response);
            }).toPromise();
        },
        appReset ({commit}) {
            commit('appReset');
        },
        appParseH2B ({dispatch, commit}, h2b) {
            commit('appParseH2B', h2b);
            if (h2b.meta != null) {
                if (h2b.meta.template != null) {
                    dispatch('appSetTemplate', h2b.meta.template);
                }
            }
            else {
                console.warn("This message is missing a meta property. No template set.");
            }
        },
        appSetTemplate ({commit}, template) {
            commit('appSetTemplate', template);
        },
        appSetMembershipId ({dispatch, commit}, pack) {
            if (pack.message.length == 14) {
                commit('appSetMembershipId', pack.message);

                dispatch('h2bSend', pack).then((response) => {
                    dispatch('appParseH2B', response);
                });
            }
            else {
                dispatch('notificationCreate', {
                    name: 'ErrorRequestSubscription',
                    title: 'Ongeldig nummer',
                    body: 'Heeft u misschien een typfout gemaakt?',
                    type: 'm-error',
                    timeout: 6000
                });

                return Promise.reject( new Error('Membership ID must be 14 characters long.') );
            }
        },
        appSelectSubscription ({commit}, subscription_id) {
            commit('appSelectSubscription', subscription_id);
        },
        appRequestSubscription ({commit}, pack) {
            return api.post( api.routes.request, pack ).do((response) => {
                return response;
            }).toPromise();
        },
        appMailAdvice ({commit}, pack) {
            return api.post( api.routes.mail, pack ).do((response) => {
                return response;
            }).toPromise();
        }
    },

    // Mutations
    mutations: {
        appLoadSubscriptionList (state, list) {
            state.subscriptionList = list;
        },
        appReset (state) {
            state.template = 'loading';
            state.token = '';
            state.question = '';
            state.meta = {};
            state.answers = {};
        },
        appParseH2B (state, h2b) {
            if (h2b.meta) {
                if (h2b.meta.advice === "1") {
                    state.advisedSubscription = h2b.message;
                    state.selectedSubscription = h2b.message;
                }
            }
            state.question = h2b.message;
            state.answers = h2b.items;
            state.meta = h2b.meta;
        },
        appSetTemplate (state, template) {
            let fromGlobals = globals.templates[template];
            if (typeof fromGlobals == 'undefined' || fromGlobals == false) {
                fromGlobals = globals.templates['_default'];
            }

            state.template = fromGlobals;
        },
        appSetMembershipId (state, id) {
            state.membershipId = id;
        },
        appSelectSubscription (state, subscription_id) {
            state.selectedSubscription = subscription_id;
        }
    },

    // Getters
    getters: {
        appTemplate: state => {
            return (state.template !== '') ? state.template : globals.templates['_default'];
        },
        appSelectedSubscription: state => {
            let subscription = state.subscriptionList[state.selectedSubscription];
            if (subscription) {
                return subscription;
            }
            else {
                console.warn("Non-valid subscription selected: " + state.selectedSubscription);
                return {};
            }
        }
    }
};

export default appStore;


