import Vue from 'vue';

const notificationDefault = {
    name: '',
    title: '',
    body: '',
    type: '',
    timeout: 3000
};

const notificationStore = {
    // Initial state
    state: {
        list: {},
    },

    // Actions
    actions: {
        notificationCreate ({commit}, notification ) {
            let newNotification = Object.assign({}, notificationDefault, notification);

            return new Promise((resolve, reject) => {
                if (newNotification.name != '') {
                    commit('notificationCreate', newNotification);
                    resolve( newNotification );
                }
                else {
                    reject( new Error('Notification must be given a name') );
                }
            });
        },
        notificationDelete ({commit}, notification) {
            setTimeout(() => {
                commit('notificationDelete', notification.name);
            }, notification.delay);
        }
    },

    // Mutations
    mutations: {
        notificationCreate (state, notification) {
            Vue.set(state.list, notification.name, notification);
        },
        notificationDelete (state, name) {
            if (typeof state.list[ name ] != 'undefined') {
                Vue.delete(state.list, name);
            }
        }
    },

    // Getters
    getters: {
    }
};

export default notificationStore;


